.App {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(to right, #0c1115, #111619, #0e1316); 
}

.App-logo {
  height: 70%;
  width: 70%;
} 

.App-header {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.mini {
  font-size: 11px;
  color: #09d3ac;
}


.codes {
  text-align: center;
  font-family: 'Bebas Neue', cursive;
}

.shrink {
  font-size: 14px;
}

h2 {
  color: whitesmoke;
  margin-left: 7vw;
  margin-right: 7vw;
  border-bottom: 1px double whitesmoke;
  width: 86%;
  display: block;
  padding-top: 10px;
}

img {
  width: 75px;
  height: 75px;
}

a:link {
  color: #8fbbf2;
}

a:visited {
  color: #697b94;
}

a:hover {
  color: #fecee2 !important;
}

@media only screen and (max-width: 405px) {
  .overflow {
    visibility: hidden;
  }
}

.hidden {
  opacity: 0;
  height: 0px;
}
/* note that show also affects tooltips */
.Show {
  opacity: 1;
  transition: opacity 0.4s ease-out;
  -ms-transition: opacity 0.4s ease-out;
  -moz-transition: opacity 0.4s ease-out;
  -webkit-transition: opacity 0.4s ease-out;
  margin: 2%;
  width: 96% !important;
  color: whitesmoke;
}

#stack {
  margin: 2%;
  width: 96% !important;
}

.progress {
background: linear-gradient(to right, #0c1115, #111619, #0e1316);
border: 0.5px solid grey;

}

@media only screen and (min-width: 770px) {
  .mobile {
    display: none;
  }
}

@media only screen and (max-width: 769px) {
  .screen {
    display: none;
  }
}

#loader {
  width: 100vw;
  height: 100vh;
}
